.NavBar li {
  transition: all 0.5s ease-in-out;
  margin:  1vw 0vw;
}
.testClass {
  height: 100vh;
  width: 450px;
  position: fixed;
  top: 0;
  left: 0;
  overflow-x: hidden;
  overflow-y: auto;
  transition: width 0.3s ease;
  display: flex;
  flex-direction: row-reverse;
}

.NavBar {
  border-right: solid 1px gray;
  height: 100vh;
  width: 250px;
  position: fixed;
  top: 0;
  left: 0;
  overflow-x: hidden;
  overflow-y: auto;
  transition: width 0.3s ease;
  display: flex;
  flex-direction: column;
}

.SearchBox {
  width: 240px;
  display: none;
  border-right: solid 1px grey;
}
.MessageBox{
  display: none;
}
.NotifBox{
  display: none;
}
.Nav-Menu {
  font-size: 1.2em;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  padding: 0;
  margin: 15vh 0 0 0;
  flex-grow: 1;
}

.Nav-Logo {
  position: fixed;
  top: 2rem;
  left: 1vw;
  width: 7rem;
  height: auto;
  max-height: 5rem;
  margin-bottom: 2rem;
}

.Nav-PlusInfo {
  position: fixed;
  bottom: 0;
  left: 0;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 250px;
  box-sizing: border-box;
}

.link-item {
  display: flex;
  align-items: center;
  padding: 0.5rem 1rem;
  width: 100%;
  transition: all 0.2s ease-in-out;
  position: relative;
  text-decoration: none;
  color: inherit;
}

.link-item::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  transition: all 0.2s ease-in-out;
  opacity: 0;
  z-index: 0;
  border-radius: 10px;
}

.link-item:hover::before {
  opacity: 1;
}

.link-item p {
  margin: 0 0 0 1.25rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  transition: color 0.2s ease-in-out;
  position: relative;
  z-index: 1;
}

.link-item svg {
  position: relative;
  z-index: 1;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
  width: 100%;
}

svg {
  width: 1.375rem;
  height: 1.375rem;
  flex-shrink: 0;
}

@media (max-width: 1440px) {
  .NavBar {
    width: 200px;
  }
  .SearchBox {
    width: calc(100vw - 200px);
    margin-left: 200px;
  }
  .Nav-PlusInfo {
    width: 200px;
  }
}

@media (max-width: 1024px) {
  .NavBar {
    width: 180px;
  }
  .SearchBox {
    width: calc(100vw - 180px);
    margin-left: 180px;
  }
  .Nav-PlusInfo {
    width: 180px;
  }
}

@media (max-width: 768px), (max-height: 650px) {
  .NavBar {
    width: 70px;
  }
  .SearchBox {
    width: calc(100vw - 70px);
    margin-left: 70px;
  }
  .Nav-Logo {
    display: none;
  }
  .link-item p {
    display: none;
  }
  .Nav-PlusInfo {
    display: none;
  }
}

@media (max-width: 480px) {
  .NavContainer {
    flex-direction: column;
  }
  .NavBar {
    width: 100%;
    height: auto;
    position: relative;
    border-right: none;
    border-bottom: solid 1px gray;
  }
  .SearchBox {
    width: 100%;
    margin-left: 0;
  }
  .Nav-Menu {
    flex-direction: row;
    justify-content: space-around;
    margin-top: 7rem;
    padding-bottom: 1rem;
  }
  .Nav-Logo {
    top: 1rem;
    width: 5rem;
  }
  .link-item {
    flex-direction: column;
    padding: 0.5rem;
    margin: 0;
  }
  .link-item p {
    display: block;
    margin: 0.5rem 0 0 0;
    font-size: 0.8em;
    text-align: center;
  }
  svg {
    margin-right: 0;
  }
  .Nav-PlusInfo {
    position: static;
    display: flex;
    justify-content: space-around;
    padding: 1rem;
    width: 100%;
  }
}
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  background-color: #1a1a1a;
  padding: 30px;
  border-radius: 12px;
  color: white;
  position: relative;
  width: 80%;
  max-width: 600px;
}

.modal-title {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 30px;
  text-align: center;
}

.upload-area {
  border: 3px dashed #666;
  border-radius: 12px;
  padding: 60px 40px;
  text-align: center;
}

.icon-container {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.icon-import {
  width: 96px;
  height: 77px;
}

.upload-text {
  font-size: 1.2rem;
  margin-bottom: 25px;
}

.select-btn {
  background-color: #0095f6;
  color: white;
  border: none;
  padding: 12px 24px;
  border-radius: 6px;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s;
}

.select-btn:hover {
  background-color: #0077c5;
}

.close-modal-btn {
  position: absolute;
  top: 15px;
  right: 15px;
  background: none;
  border: none;
  color: white;
  font-size: 28px;
  cursor: pointer;
}